@import url(https://fonts.googleapis.com/css2?family=Libre+Franklin:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400&display=swap);
:root {
	--darkAccent: rgba(2, 0, 36, 1);
	--lightAccent: #f3d6e2;
}

* {
	box-sizing: border-box;
}

*,
html {
	line-height: 1em;
	padding: 0;
	margin: 0;
	font-family: 'Libre Franklin', sans-serif;
}

body {
	margin: 0;
	padding: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}

h3 {
	font-size: 2.6em;
	text-align: center;
	padding: 1em;
	font-weight: 500;
}
nav {
	width: 100%;
	position: -webkit-sticky;
	position: sticky;
	z-index: 10;
	top: 0;
	padding: 1.2em 0;
	color: white;
	background-color: var(--darkAccent);
	max-height: 4em;
}

nav span {
	font-size: 18px;
	border-bottom: 1px solid transparent;
	transition: border-bottom 0.2s ease;
	transition-delay: 0.25s;
	margin: 1em;
	padding-bottom: 0.3em;
}

nav span:hover {
	cursor: pointer;
	border-bottom: 1px solid white;
}

nav span:hover i {
	color: gold;
}

nav span.activeClass i {
	color: gold;
}

nav span.activeClass {
	border-bottom-color: var(--lightAccent);
}
section {
	/* padding-top: 100px; */
	border-bottom: 1px solid lightgrey;
}
.appContainer {
	height: auto;
	width: 100vw;
	display: flex;
	flex-direction: column;
	margin: 0;
	padding: 0;
}
